import { Typography, Box } from "@mui/material/";
import Resume from "./resume";
import { useEffect, useRef } from "react";
import gsap from "gsap";

export default function Intro() {
  const titleRef = useRef();
  const onLoad = () => {
    gsap
      .timeline()
      .fromTo(
        ".letter",
        {
          x: -100,
          opacity: 0,
        },
        {
          x: 0,
          opacity: 1,
          stagger: 0.1,
          delay: 0.3,
        }
      )
      .to(".letter", {
        margin: "0 5px",
        delay: 5.0,
        duration: 0.5,
      });
  };

  useEffect(() => {
    onLoad();
  }, []);

  return (
    <Box
      id="intro"
      sx={{ display: "flex", justifyContent: "center", paddingTop: "8rem" }}
    >
      <Box sx={{ width: "60%", textAlign: "start" }}>
        <Typography variant="h3" sx={{ color: "primary.light" }}>
          Enchant, moi cest
        </Typography>
        <Typography
          className="title"
          ref={titleRef}
          variant="h1"
          sx={{ color: "info.main" }}
        >
          <span className="letter">L</span>
          <span className="letter">o</span>
          <span className="letter">u</span>
          <span className="letter">i</span>
          <span className="letter">s</span>
          <span className="letter"> </span>
          <span className="letter">N</span>
          <span className="letter">o</span>
          <span className="letter">w</span>
          <span className="letter">a</span>
          <span className="letter">k</span>
        </Typography>
        <Typography variant="h3" sx={{ color: "primary.light" }}>
          Un dveloppeur investi, prt  relever vos dfis
        </Typography>
        <Typography variant="h4" sx={{ color: "primary.light" }}>
          Dveloppeur web passionn et polyvalent, je suis  la recherche dune
          alternance pour mon Master afin de continuer  progresser au contact
          de projets concrets. Curieux et adaptable, jaime explorer de
          nouvelles technologies et relever des dfis varis.
        </Typography>
        <Typography
          variant="h4"
          sx={{ color: "primary.light", marginTop: "20px" }}
        >
          Le travail en quipe est au cur de ma mthode : je minvestis
          pleinement, avec rigueur et persvrance, pour contribuer efficacement
          aux objectifs communs. Mon ambition ? Devenir un atout incontournable
          au sein de lentreprise qui me fera confiance.
        </Typography>
        <Resume />
      </Box>
    </Box>
  );
}
